import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router-dom';
import Navbar from "../../components/Navbar/Navbar";
import { getReports, subscribeReport, unsubscribeReport} from '../../actions/oar-reports-actions'
import { getUserInfo } from '../../actions/auth-actions'
import Loader from "../App/Loader";
import { Icon } from 'react-icons-kit'
import {github} from 'react-icons-kit/icomoon/github'
import {ic_help} from 'react-icons-kit/md/ic_help'
import {ic_settings_applications} from 'react-icons-kit/md/ic_settings_applications'
import './ReportsManager.css'
class ReportsManager extends Component {

    constructor(props){
        super(props);
        this.state={
            reportName:""
        }
    }

    componentDidMount() {
        this.props.getReports();
        this.props.getUserInfo()
    }

    render() {
        let { reports  } = this.props
        return (
            <div>
                {reports.loading && <Loader/>}
                <Navbar/>
                <div className="jumbotron" style={{minHeight:'100vh'}}>
                    <h1>OAR Reports Manager</h1>
                    <ul className="breadcrumb">
                        <li><Link to="home">Home</Link></li>
                        <li>OAR Reports Manager</li>
                    </ul>
                    <hr size="30"/>
                    <form action="">
                        <div className={"row"}>
                            <div className={"col-md-3"}>
                                <div className="form-group">
                                    <label htmlFor="email">Search by report name</label>
                                    <input type="text" className="form-control" id="reportName" onChange={(reportName) => this.setState({reportName:reportName.target.value})}/>
                                </div>
                            </div>
                            <div className={"col-md-2"}>
                                <button type="submit" className="btn btn-info" style={{marginTop:'2rem'}}>Search</button>
                            </div>
                        </div>



                    </form>
                    <table className="table table-striped table-bordered">
                        <thead>
                            <tr>
                                <th>Report Name</th>
                                <th>System</th>
                                <th>Links</th>
                                <th>Frequency</th>
                                <th>Created By</th>
                                <th>Created On</th>
                                <th>Subscribe/Unsubscribe</th>
                            </tr>
                        </thead>
                        <tbody>
                        {
                            reports.reports && reports.reports
                                .filter(item => item.reportName.toLowerCase().includes(this.state.reportName.toLowerCase()))
                                .map((item, index) => (
                                <tr>
                                    <td>{item.reportName}</td>
                                    <td>{item.system.toUpperCase()}</td>
                                    <td style={{display:'inlineBlock', minWidth:'6rem'}}>
                                        {item.gitUrl &&  <a href={item.gitUrl} target={"blank"} style={{display:'inlineBlock', marginRight:'0.3rem', color:'black'}}><Icon icon={github} size={17}/></a>}
                                        {item.baasUrl && <a href={item.baasUrl} target={"blank"} style={{display:'inlineBlock', marginRight:'0.3rem', color:'black'}}><Icon icon={ic_settings_applications} size={21}/></a>}
                                        {item.helpUrl && <a href={item.helpUrl} target={"blank"} style={{display:'inlineBlock', marginRight:'0.3rem', color:'black'}}><Icon icon={ic_help} size={19}/></a>}
                                    </td>
                                    <td>{(item.frequency/60000)>60?(( (item.frequency/3600000)>24? ((item.frequency/86400000) + " day(s)"):((item.frequency/3600000) + " hour(s)") )): ((item.frequency/60000) + " min(s)")}</td>
                                    <td>{item.userId}</td>
                                    <td>{item.createTs}</td>
                                    {!item.isSubscribed && <td><button type="button" className="btn btn-success" style={{fontSize:'1rem', height:'2rem', marginTop:'-0.4rem' ,paddingTop:'3px', outline:'none !important'}} onClick={()=>this.props.subscribeReport(item.reportName)}>Subscribe</button></td>}
                                    {item.isSubscribed && <td><button type="button" className="btn btn-info" style={{fontSize:'1rem', height:'2rem', marginTop:'-0.4rem' ,paddingTop:'3px', color:'white', outline:'none !important'}} onClick={()=>this.props.unsubscribeReport(item.reportName)}>Unsubscribe</button></td>}
                                </tr>
                            ))
                        }
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

ReportsManager.propTypes = {
    reports:PropTypes.object,
    getUserInfo:PropTypes.func.isRequired,
    getReports: PropTypes.func.isRequired,
    subscribeReport: PropTypes.func.isRequired,
    unsubscribeReport: PropTypes.func.isRequired
}

const mapStateToProps = state  => ({
    reports:state.reports
})

export default connect(mapStateToProps, { getReports, getUserInfo, subscribeReport, unsubscribeReport})(withRouter(ReportsManager))

