import {DATA_LOADING, GET_REPORTS, GET_USER_REPORTS, REPORTS_LOADING} from '../actions/constants';

const initialState = {

};

export default function(state = initialState, action) {
    switch(action.type) {
        case GET_REPORTS:
            return {
                ...state,
                loading:action.payload.loading,
                reports: action.payload.reports,
                userReports:action.payload.userReports
            };
        case REPORTS_LOADING:
            return {
                ...state,
                loading:action.payload.loading
            };
        case GET_USER_REPORTS:
            return {
                ...state,
                loading:action.payload.loading,
                userReports: action.payload.userReports
            }
        case DATA_LOADING:
            return {
                ...state,
                loading: action.payload.loading
            }
        default:
            return state;
    }
}