import axios from 'axios';
import {
    BASE_URL,
    DATA_LOADING,
    GET_ERRORS,
    GET_REPORTS
} from './constants'

export const getReports = () => dispatch => {
    dispatch({
        type: DATA_LOADING,
        payload: {
            loading:true
        }
    });
    axios.get(BASE_URL + "getOarReports")
        .then(response => {
            let allReports = response.data;
            let body = {
                email: localStorage.getItem('nirvanaEmail').toLowerCase()
            }
            axios.post(BASE_URL + "getSubscriptions", body)
                .then(response => {
                    let userReports = response.data;

                    for(let i=0; i<allReports.length; i++){
                        if(userReports.includes(allReports[i].reportName)){
                            allReports[i].isSubscribed = true;
                        }
                        else{
                            allReports[i].isSubscribed = false;
                        }
                    }
                    dispatch({
                        type: GET_REPORTS,
                        payload: {
                            loading:false,
                            reports:allReports,
                            userReports:userReports
                        }
                    });
                })
                .catch(error => {
                    dispatch({
                        type: GET_ERRORS,
                        payload: error
                    });
                })


        })
        .catch(error => {
            dispatch({
                type: GET_ERRORS,
                payload: error
            });
        })
}

export const subscribeReport = (reportName) => dispatch => {
    dispatch({
        type: DATA_LOADING,
        payload: {
            loading:true
        }
    });
    let body = {
        "reportName":reportName,
        "email": localStorage.getItem('nirvanaEmail').toLowerCase()
    }
    axios.post(BASE_URL + "subscribeOarReport", body)
        .then(response => {
            console.log(response)
            if(response.data.message==="SUCCESS"){
                dispatch({
                    type: DATA_LOADING,
                    payload: {
                        loading:true
                    }
                });
                axios.get(BASE_URL + "getOarReports")
                    .then(response => {
                        let allReports = response.data;
                        let body = {
                            email: localStorage.getItem('nirvanaEmail').toLowerCase()
                        }
                        axios.post(BASE_URL + "getSubscriptions", body)
                            .then(response => {
                                let userReports = response.data;

                                for(let i=0; i<allReports.length; i++){
                                    if(userReports.includes(allReports[i].reportName)){
                                        allReports[i].isSubscribed = true;
                                    }
                                    else{
                                        allReports[i].isSubscribed = false;
                                    }
                                }
                                dispatch({
                                    type: GET_REPORTS,
                                    payload: {
                                        loading:false,
                                        reports:allReports,
                                        userReports:userReports
                                    }
                                });
                            })
                            .catch(error => {
                                dispatch({
                                    type: GET_ERRORS,
                                    payload: error
                                });
                            })

                    })
                    .catch(error => {
                        dispatch({
                            type: GET_ERRORS,
                            payload: error
                        });
                    })
            }
        })
        .catch(error => {
            dispatch({
                type: GET_ERRORS,
                payload: error
            });
        })
}

